}
.content__container {
  font-weight: 600;
  width: 100%;
  overflow: hidden;
  height: 6rem;
  text-align: center;
  padding: 0 7rem;
  text-transform: uppercase;
  letter-spacing: .5rem;

}

.content__container__list {
  list-style: none;
  -webkit-animation-name: changeText;
  -webkit-animation-duration: 10s;
  animation-name: changeText;
  padding-top: 0;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.content__container__list__item {
  line-height: 2em;
  font-size: 3em;
  text-align: center;
  margin: 0;
  padding-top: 0;
}

/**the animation of the text*/
@-webkit-keyframes changeText {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  25% {
    -webkit-transform: translate3d(0, -25%, 0);
            transform: translate3d(0, -25%, 0);
  }
  50% {
    -webkit-transform: translate3d(0, -50%, 0);
            transform: translate3d(0, -50%, 0);
  }
  75% {
    -webkit-transform: translate3d(0, -75%, 0);
            transform: translate3d(0, -75%, 0);
  }
  100%{
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
  }
}
@keyframes changeText {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  25% {
    -webkit-transform: translate3d(0, -25%, 0);
            transform: translate3d(0, -25%, 0);
  }
  50% {
    -webkit-transform: translate3d(0, -50%, 0);
            transform: translate3d(0, -50%, 0);
  }
  75% {
    -webkit-transform: translate3d(0, -75%, 0);
            transform: translate3d(0, -75%, 0);
  }
  100%{
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
  }
}

.get_started_button{
  position: absolute;
  color: yellow;
  text-transform: uppercase;
  padding: 15px 80px;
  display: inline-block;
  border-radius: 100px;
  font-weight: 600;
  left: 50%;
  -webkit-transform: translate(-50%, -80%);
          transform: translate(-50%, -80%);
  top: 80%;
  background-color: black;
  cursor: pointer;
}

.get_started_button:link,
.get_started_button:visited {
  text-decoration: none;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}



.get_started_button:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  /**x, y, blur, color)**/
}

.get_started_button:active {
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

/**create the get_started_button effec**/
.get_started_button::after {
  content: "";
  display: inline-block;
  /**to match button**/
  height: 100%;
  width: 100%;
  border-radius: 100px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}

.get_started_button-white::after {
  background-color: #fff;
}

/* when we hover the button show the after element with the styles below added**/
.get_started_button:hover::after {
  -webkit-transform: scaleX(1.4) scaleY(1.6);
          transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}
.get_started_button::after{
  z-index: -1;
  content: " ";
  display: inline-block;
  background-color: black;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

