}


.slide-button{
  color: black;
  padding: 10px;
  position: relative;
  top: 5rem;
  margin: 5%;
  padding: 10px 30px;
  text-decoration: none;
  border: 1px solid black;
	background-color: yellow;
  font-weight: bolder;
  border-radius: 5px;
	cursor: pointer;
	
}
 

